/* eslint-disable camelcase */
<template>
  <div>
      <Loading :loading="loading"/>
      <div class="main-list">
          <Empty v-if="!loading && lists.length == 0"/>
          <div class="lists" v-if="!loading && lists.length > 0">
              <div class="item" v-for="(item, key) in lists" :key="key">
                <van-card
                    :num="item.amount"
                    :price="item.price"
                    desc=""
                    thumb="https://img.yzcdn.cn/vant/ipad.jpeg"
                    >
                    <template #title>
                        <p @click="$router.push('/order/' + item.id)">{{item.title}}</p>
                    </template>
                    <template #tags>
                        <van-tag plain type="danger">标签</van-tag>
                        <van-tag plain type="danger">标签</van-tag>
                    </template>
                    <template #footer>
                        <van-button size="mini" @click="onPay(2, item.id)">支付宝</van-button>
                        <van-button size="mini" @click="onPay(1, item.id)">微信支付</van-button>
                    </template>
                </van-card>
              </div>

          </div>
          <div class="">
            <van-button size="mini" @click="onBuy">生成临时商品</van-button>
          </div>
      </div>

  </div>
</template>
<script>
import { Toast } from 'vant'
import http from '@/utils/axios'
import Loading from '@/components/common/Loading'
import Empty from '@/components/common/Empty'
export default {
  data () {
    return {
      loading: false,
      lists: [],
      const_query: {
        limit: 15
      },
      query: {
        page: 1
      }
    }
  },
  components: {
    Loading, Empty
  },
  methods: {
    initData () {
      this.page = 1
      this.lists = []
      this.fetch()
    },
    fetch () {
      if (this.loading) { return }
      this.loading = true
      http.ajaxGet('order', {...this.query, ...this.const_query}, res => {
        this.loading = false
        this.query.page = res.data.current_page
        if (res.data.data.length > 0) {
          this.lists = this.query.page === 1 ? res.data.data : this.lists.concat(res.data.data)
        }
      })
    },
    // eslint-disable-next-line camelcase
    onPay (payment_method, order_id) {
      // eslint-disable-next-line camelcase
      http.ajaxPost('order/' + order_id + '/pay', {payment_method}, res => {
        if (res.status === 'success') {
          // eslint-disable-next-line camelcase
          if (payment_method === 2) {
            // //支付宝
            const payDiv = document.getElementById('payDiv')
            if (payDiv) {
              document.body.removeChild(payDiv)
            }
            const div = document.createElement('div')
            div.id = 'payDiv'
            div.innerHTML = res
            document.body.appendChild(div)
            document.getElementById('payDiv').getElementsByTagName('form')[0].submit()
          }
        } else {
          Toast(res.message)
        }
      })
    },
    onBuy () {
      // eslint-disable-next-line camelcase
      http.ajaxPost('order', {}, res => {
        if (res.status === 'success') {
          this.initData()
        }
      })
    }
  },
  mounted () {
    this.initData()
  }
}
</script>
<style scoped>
.item {margin: 5px 0;}
</style>



